<script>

    import {Route} from "tinro";

    import {
        Core,
        View
    } from "./Core";

    import {
        Page
    } from "./Components";

    import {
        Login,
        Home,
        Panel
    } from "./Pages";


    const pages = [
        {
            url: '/',
            component: Home,
            auth: false,
            header: true,
            arrow: true,
            userProfile: true,
            video: true,
            menu: [
                {
                    text: 'Главная'
                },
                {
                    text: 'Информация'
                },
                {
                    text: 'Тарифы'
                },
            ]
        },
        {
            url: '/login',
            component: Login,
            auth: false,
            header: true,
            arrow: false,
            userProfile: false,
            video: false,
            menu: []
        },
        {
            url: '/panel',
            component: Panel,
            auth: true,
            header: true,
            arrow: false,
            userProfile: false,
            video: false,
            menu: []
        }
    ]

</script>

<Core>
    {#each pages as page}
        <Route path={page.url}>
            <View auth={page.auth}>
                <svelte:fragment>
                    <Page header={page.header}
                          arrow={page.arrow}
                          menu={page.menu}
                          userProfile={page.userProfile}
                          video={page.video}
                    >
                        <svelte:component this={page.component}>
                        </svelte:component>
                    </Page>
                </svelte:fragment>
            </View>
        </Route>
    {/each}
</Core>

<style lang="scss">



</style>

