<script>

    import {getContext} from "svelte";
    import {router} from "tinro";

    const
        page = getContext("page"),
        test = getContext("test"),
        fetchA = getContext("fetch"),
        options = getContext("options"),
        access_token = getContext("access_token");

    let user, fb_success = false;

    const getInfo = async () => {
        const resp = await fetchA(`${options.b_url}/user/getInfo`, {
            method: "POST"
        });

        if(!resp.ok) {
            $access_token = false;
            localStorage.removeItem('access_token');
            router.goto('/');
        }

        const data = (await resp.json()).data;

        user = {...data.user}
        fb_success = data.fb_success;
        console.log(user)
    }


</script>

{#await getInfo()}
    PROLOADER
{:then data}
    <div class="container">
        <div class="user">
            <div class="user__container" on:click={() => {test()}}>
                <h1>{user.username}</h1>

            </div>
        </div>
    </div>
    <div class="panel">
        <h1>THIS PANEL</h1>
    </div>
{/await}

<style lang="scss">
    .user {
      padding: 8% 0;
      width: 100%;
      height: 100vh;
      color: white;

      &__container  {
        width: 100%;
        height: 100%;
        background: #181F29;
        padding: 3%;
      }
    }
    .panel {
        width: 100%;
        height: 100vh;
        color: white;
    }
</style>