<script>

    import {createEventDispatcher, getContext} from "svelte";
    import {router} from "tinro";

    const
        page = getContext("page"),
        user = getContext("user"),
        access_token = getContext("access_token"),
        options = getContext("options");

    let
        login = '',
        password = '';

    const dispatch = createEventDispatcher();

    const changePage = (num) => {
        login = '';
        password = '';
        $page = num
    }

    const loginGo = async () => {
        if(!login || !password)
            return

        const resp = await fetch(`${options.b_url}/auth`, {
            method: "POST",
            body: new URLSearchParams({
                login,
                password
            })
        })

        if(resp.ok) {
            const json = await resp.json();
            console.log(json)
            $user = json.data.username;
            localStorage.setItem('access_token', json.data.access_token)
            $access_token = json.data.access_token;
            router.goto('/')
        }
    }

    const registrationGo = async () => {
        if(!login || !password)
            return

        const resp = await fetch(`${options.b_url}/registration`, {
            method: "POST",
            body: new URLSearchParams({
                login,
                password
            })
        })

        if(resp.ok) {
            const json = await resp.json();
            console.log(json)
            $user = json.data;
            $page = 1;
        }
    }

</script>

<div class="container">
    <div class="login">
        <div class="login__block">
            <div class="login__text" class:animate={$page === 1}>
                <div class="login__title" class:animate={$page === 1}>
                    <h1>Helper <span>SMM</span></h1>
                    <p>Авторизация</p>
                </div>
            </div>
        </div>
        <div class="login__block right">
            <div class="form">
                <div class="form__input">
                    <input type="text" id="login" bind:value={login} placeholder="Введите логин">
                </div>
                <div class="form__input">
                    <input type="password" bind:value={password} placeholder="Введите пароль">
                </div>
                <div class="form__btn">
                    <div class="btn" on:click={loginGo}>
                        <p>Авторизоваться</p>
                    </div>
                    <p>У вас нет аккаунта? <span on:click={() => {changePage(2)}}>Регистрация</span></p>
                </div>
            </div>
        </div>
    </div>
    <div class="login">
        <div class="login__block">
            <div class="login__text" class:animate={$page === 2}>
                <div class="login__title" class:animate={$page === 2}>
                    <h1>Helper <span>SMM</span></h1>
                    <p>Регистрация</p>
                </div>
            </div>
        </div>
        <div class="login__block right">
            <div class="form">
                <div class="form__input">
                    <input type="text" bind:value={login} placeholder="Введите логин">
                </div>
                <div class="form__input">
                    <input type="text" bind:value={password} placeholder="Введите пароль">
                </div>
                <div class="form__btn">
                    <div class="btn" on:click={registrationGo}>
                        <p>Авторизоваться</p>
                    </div>
                    <p>У вас уже есть аккаунт? <span on:click={() => {changePage(1)}}>Авторизация</span></p>
                </div>
            </div>
        </div>
    </div>
</div>


<style lang="scss">
  .login {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 8%;

    &__block {
      width: 50%;
      height: 100%;
      display: flex;
      justify-content: center;
      flex-direction: column;

      &.right {
        justify-content: flex-start;
        flex-direction: row;
        align-items: center;
      }
    }

    &__text {
      color: white;
      padding-left: 15px;
      position: relative;
      overflow: hidden;

      &.animate {
        &:before {
          background: rgba(92, 163, 91, 0.3);
          animation: animateHeightTop 2s ease;
        }
      }

      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 1px;
      }
    }

    &__title {
      font-size: 28px;
      padding: 20px 0;

      &.animate {
        animation: animateAuthText 1s ease-in-out;
      }
      & p {
        font-family: "SFLight", serif;
        font-weight: 300;
        font-size: 22px;
        margin-top: -5px;
        color: #5CA35B;
      }
    }
  }

  .form {
    padding: 10% 5%;
    background: #273140;
    border-radius: 15px;
    height: 40%;
    width: 50%;
    position: relative;

    &__input {
      & input {
        font-family: "SFFont", serif;
        outline: none;
        border: none;
        border-bottom: 1px solid rgba(92, 163, 91, 0.3);
        background: transparent;
        padding: 10px 2px;
        transition: .2s;
        color: white;
        font-size: 18px;
        margin-bottom: 5%;

        &::placeholder {
          color: rgba(92, 163, 91, 0.3);
        }

        &:focus {
          border-color: rgba(92, 163, 91, 1);
        }
      }
    }

    &__btn {
      position: absolute;
      left: 0;
      bottom: 3%;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      & p {
        font-size: 10px;
        color: rgba(250, 250, 250, .5);
        transition: .2s;

        & span {
          cursor: pointer;
          transition: .2s;
          color: rgba(92, 163, 91, .8);
        }

        &:hover {
          & span {
            color: rgb(88, 187, 87);
          }
        }
      }
    }
  }

  .btn {
    padding: 10px 15px;
    background: rgba(92, 163, 91, 0.3);
    cursor: pointer;
    transition: .2s;
    border-radius: 8px;
    margin-bottom: 12px;

    &:hover {
        background: rgba(92, 163, 91, 1);
    }
  }

  @keyframes animateAuthText {
    0% {
      transform: translateX(-1000px);
    }
    50% {
      transform: translateX(-1000px);
    }
    100% {
      transform: translateX(0);
    }
  }
</style>