<script>

    import {getContext} from "svelte";
    import {router} from "tinro";

    const
        isAuth = getContext("isAuth");

    export let auth = false;

    const checkAuth = async () => {
        const resp = await isAuth();

        if(!resp)
            router.goto('/')
    }

</script>

{#if auth}
    {#await checkAuth()}

    {:then el}
        <slot />
    {/await}
{:else}
    <slot />
{/if}