<script>

    import {router} from "tinro";
    import {getContext, setContext} from "svelte";
    import {writable} from "svelte/store";

    const
        user = writable(false);

    const
        access_token = writable(localStorage.getItem('access_token') ? localStorage.getItem('access_token') : false),
        options = getContext('options');

    const isAuth = async () => {

        if(!$access_token)
            router.goto('/')


        const resp = await fetch(`${options}/isAuth`, {
            method: "POST",
            body: new URLSearchParams({
                access_token: $access_token
            })
        })

        if(!resp.ok) {
            $access_token = false;
            localStorage.removeItem('access_token');
            return false;
        }

        return true;

    }

    const fetchWithAuth = async (url, options) => {
        if(!$access_token) router.goto('/');

        const _isAuth = await isAuth();

        if(!_isAuth) router.goto('/');

        if(!options.headers) options.headers = {};

        options.headers.Authorization = `Bearer ${$access_token}`;
        return fetch(url, options)
    }

    setContext('user', user);
    setContext('access_token', access_token);
    setContext('isAuth', isAuth);
    setContext('fetch', fetchWithAuth);

</script>

<slot />