<script>
    import {getContext, onMount, setContext} from "svelte";
    import {writable} from "svelte/store"

    const
        access_token = getContext("access_token"),
        user = getContext("user");

    export let arrow = false;
    export let header = false;
    export let menu = [];
    export let userProfile = true;
    export let video = true;

    let
        thumbTranslate = "translateY(0)",
        numArrow = writable(1),
        reverse = false,
        storeChange = false,
        beforeNum = 1;

    const thumb = (num) => {
        if(!document.querySelector('.Page'))
            return
        const height = document.querySelector('.Page').offsetHeight;
        const thumbGo = (num === 1 ? 0 : num - 1) *  height;
        console.log(-thumbGo + beforeNum > num ? 150 : -150)
        thumbTranslate = `translateY(${(beforeNum > num ? -thumbGo+150 : -thumbGo-150)}px)`;
        if(storeChange) {
            storeChange = false;
        } else {
            $numArrow = num;
        }
        beforeNum = num;
        setTimeout((numReverse = num) => {
            if(numReverse !== $numArrow)
                return
            thumbTranslate = `translateY(${-thumbGo}px)`;
        }, 500)
    }

    $: {
        if($numArrow === 3) {
            reverse = true
        } else if($numArrow === 1) {
            reverse = false
        }
    }

    $: {
        if($numArrow) {
            storeChange = true;
            thumb($numArrow)
        }
    }

    setContext("page", numArrow)

</script>

<div class="Page">
    <div class="content">
        {#if header}
            <div class="home">
                <div class="container">
                    <div class="header" class:center={menu.length === 0 && !userProfile}>
                        {#if menu.length > 0}
                            <div class="header__menu">
                                <ul class="menu">
                                    {#each menu as item, i}
                                        <li class="menu__item"
                                            class:active={$numArrow === i+1}
                                            on:click={() => {thumb(i+1)}}>
                                            {item.text}
                                        </li>
                                    {/each}
                                </ul>
                            </div>
                        {/if}
                        <div class="header__logo" style="cursor: pointer;">
                            <a href="http://192.168.2.50:5000/"><img src="./images/logo.png" alt=""></a>
                        </div>
                        {#if userProfile}

                            <div class="header__menu">
                                <ul class="menu">
                                    {#if !$access_token}
                                        <li class="menu__item">
                                            <a href="/login">Авторизация</a>
                                        </li>
                                    {:else}
                                        <li class="menu__item">
                                            <a href="/panel">Панель управления</a>
                                        </li>
                                    {/if}
                                </ul>
                            </div>
                        {/if}
                    </div>
                </div>
            </div>
        {/if}
        <div class="page__thumb" on:changePage={(i) => {thumb(i)}} style="transform: {thumbTranslate};">
            <slot  />
        </div>
        {#if arrow}
            <div class="arrow"
                 class:reverse={reverse}
                 on:click={() => {
            if($numArrow !== 3 && !reverse) {
                $numArrow += 1;
                if($numArrow === 3)
                    reverse = true
            } else {
                $numArrow -= 1;
                if($numArrow === 1)
                    reverse = false
            }
            thumb($numArrow)
         }}
            >
                <img src="./icons/down-arrow.png" alt="">
            </div>
        {/if}
    </div>
    {#if video}
        <div class="video">
            <video autoplay loop muted src="./videos/bg.mp4">
            </video>
            <div class="video__bg">

            </div>
        </div>
    {/if}
</div>

<style lang="scss">

  @mixin colorAnimate($colorStart, $colorEnd, $transition) {
    animation: animateColor $transition ease;
    @keyframes animateColor {
      from {
        color: $colorStart;
      }
      to {
        color: $colorEnd;
      }
    }
  }

  .video {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: .09;
    pointer-events: none;
    z-index: 1;

    & video {
      pointer-events: none;
    }
  }

  .content {
    z-index: 2;
  }


  .arrow {
    position: absolute;
    left: 3%;
    bottom: 7%;
    filter: brightness(0) invert(1);
    animation: animateArrowDown 1s infinite;
    cursor: pointer;
    transition: .2s;

    &.reverse img {
      transform: rotate(180deg);
      transition: .2s;
      animation: animateArrowUp 1s infinite;
    }

    &:hover {
      animation-play-state: paused;
    }
  }

  .header {
    display: flex;
    justify-content: space-between;

    &.center {
      justify-content: center;
    }
    align-items: center;
    width: 70%;
    padding: 25px 0;
    color: rgba(118, 134, 159, 0.8);
    position: fixed;
    z-index: 99;
  }

  .page__thumb {
    transition: .5s ease-in-out;
    min-width: 100%;
    min-height: 100vh;
  }

  .menu {
    display: flex;
    list-style: none;

    &__item {
      margin-right: 25%;
      cursor: pointer;
      transition: .2s;
      transform: scale(.98);
      font-size: 18px;
      font-weight: 300;

      & a {
        text-decoration: none;
        color: rgba(118, 134, 159, 0.8);

        &:hover {
          color: #5CA35B;
        }
      }

      &.active {
        color: #5CA35B;
        transform: none;
      }

      &:last-child {
        margin: 0;
      }

      &:hover {
        color: #5CA35B;
        transform: none;
      }
    }
  }


  @keyframes animateArrowDown {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(20px);
    }
    100% {
      transform: translateY(0);
    }
  }

  @keyframes animateArrowUp {
    0% {
      transform: translateY(0) rotate(180deg);
    }
    50% {
      transform: translateY(-20px) rotate(180deg);
    }
    100% {
      transform: translateY(0) rotate(180deg);
    }
  }


</style>